.cards {
   display: grid;
   grid-template-rows: 15rem max-content max-content;
   gap: 1.5rem;
   padding: 1.25rem;

   background: var(--white-color, white);
   box-shadow: 0 0 2.5px var(--black-color);
   text-align: center;

   & img {
      width: 100%;
      height: 100%;
      object-fit: cover;
   }
}
